import React, { useRef } from "react";
import { useGLTF } from "@react-three/drei";

export function Community(props) {
  const { nodes, materials } = useGLTF("/glossarySelected/community.glb");
  return (
    <group {...props} dispose={null}>
      <group //position={[0.14, -1.98, -4.51]}
      >
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere178.geometry}
          material={materials["glass.004"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere178_1.geometry}
          material={materials["glass.001"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere178_2.geometry}
          material={materials.outline}
        />
      </group>
    </group>
  );
}
export default Community;

useGLTF.preload("/glossarySelected/community.glb");
